@charset "UTF-8";
@import '@fancyapps/fancybox/dist/jquery.fancybox.min.css';
@import 'swiper/swiper-bundle.min.css';
@import './css/datepicker.css';
@font-face {
  font-family: "Frank";
  src: url("../fonts/Frank/Frank-Bold.woff2") format("woff2"), url("../fonts/Frank/Frank-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Frank";
  src: url("../fonts/Frank/Frank-Light.woff2") format("woff2"), url("../fonts/Frank/Frank-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Frank";
  src: url("../fonts/Frank/Frank-Medium.woff2") format("woff2"), url("../fonts/Frank/Frank-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Frank";
  src: url("../fonts/Frank/Frank-Regular.woff2") format("woff2"), url("../fonts/Frank/Frank-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.compensate-for-scrollbar {
  width: calc(100% - 6px);
}

.fancybox-is-open .fancybox-bg {
  background-color: black;
  opacity: 0.8;
}

html {
  height: 100%;
  font-size: 16px;
  font-weight: 300;
}
html.scroll {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  min-height: 100%;
  background-color: #F2F2F2;
  font-family: "Frank", sans-serif;
  padding: 0;
  margin: 0;
  color: #373334;
  display: flex;
  flex-direction: column;
}
body::-webkit-scrollbar {
  width: 6px;
  appearance: none;
  -webkit-appearance: none;
}
body::-webkit-scrollbar-track {
  border-radius: 2px;
  background: #959595;
}
body::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #9F0023;
  border: 2px solid #9F0023;
}
body.fancybox-active {
  overflow: hidden;
}
body.mobilemenu-opened {
  position: relative;
  overflow: hidden;
}
@media screen and (min-width: 1023px) {
  body.mobilemenu-opened {
    width: calc(100% - 6px);
  }
}

select, input, textarea, button {
  font-family: "Frank", sans-serif;
  appearance: none;
}

ul, ol, h1, h2, h3, h4, h5, a {
  list-style: none;
  padding: 0;
  margin: 0;
  text-decoration: none;
}

iframe {
  max-width: 100%;
}

.wrap {
  width: 100%;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.wrap main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.wrapper {
  width: 100%;
  max-width: 1532px;
  margin: 0 auto;
  position: relative;
  padding: 0 22px;
  display: flex;
  flex-direction: column;
}

section {
  width: 100%;
  margin-bottom: 85px;
}
@media screen and (max-width: 768px) {
  section {
    margin-bottom: 55px;
  }
}

.inner section {
  padding-top: 55px;
}
@media screen and (max-width: 768px) {
  .inner section {
    padding-top: 35px;
  }
}

*, *::after, *::before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}

button {
  cursor: pointer;
  background: none;
  border: none;
}
button:active {
  outline: none;
}
button:focus {
  outline: none;
}

input, textarea {
  border: none;
}

a {
  color: inherit;
}
a:active {
  outline: none;
}
a:focus {
  outline: none;
}

input:focus, input:active, select:focus, select:active, textarea:focus, textarea:active {
  outline: none;
}

p {
  line-height: 1.6;
  color: #2C2D2E;
  margin: 0;
  padding: 0;
}

h1 {
  font-weight: bold;
  font-size: 100px;
  line-height: 90%;
  color: #333333;
}
@media screen and (max-width: 1890px) {
  h1 {
    font-size: 86px;
  }
}
@media screen and (max-width: 1023px) {
  h1 {
    font-size: 70px;
  }
}
@media screen and (max-width: 768px) {
  h1 {
    font-size: 54px;
  }
}
@media screen and (max-width: 425px) {
  h1 {
    font-size: 40px;
    line-height: 1;
  }
}

h2 {
  font-weight: bold;
  font-size: 70px;
  line-height: 90%;
}
@media screen and (max-width: 1890px) {
  h2 {
    font-size: 60px;
  }
}
@media screen and (max-width: 1023px) {
  h2 {
    font-size: 50px;
  }
}
@media screen and (max-width: 768px) {
  h2 {
    font-size: 36px;
  }
}
@media screen and (max-width: 425px) {
  h2 {
    font-weight: 500;
    font-size: 22px;
    line-height: 1.25;
  }
}

h3 {
  font-weight: bold;
  font-size: 52px;
  line-height: 1.25;
}
@media screen and (max-width: 1890px) {
  h3 {
    font-size: 44px;
  }
}
@media screen and (max-width: 1023px) {
  h3 {
    font-size: 32px;
  }
}
@media screen and (max-width: 768px) {
  h3 {
    font-size: 24px;
  }
}
@media screen and (max-width: 425px) {
  h3 {
    font-weight: 500;
    font-size: 22px;
    line-height: 1.3;
  }
}

h4 {
  line-height: 1.15;
  font-weight: 500;
  font-size: 24px;
}
@media screen and (max-width: 1023px) {
  h4 {
    font-size: 22px;
  }
}
@media screen and (max-width: 768px) {
  h4 {
    font-size: 20px;
  }
}
@media screen and (max-width: 425px) {
  h4 {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.5;
  }
}

h5 {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.35;
}
@media screen and (max-width: 425px) {
  h5 {
    font-size: 16px;
  }
}

.fadeInUp {
  opacity: 0;
  transform: translate3d(0, 35px, 0);
  transition: opacity 0.5s linear, transform 0.7s ease;
  transition-delay: 0.25s;
}
.fadeInUp::before {
  transition: opacity 0.3s;
  opacity: 0;
}
.fadeInUp.js-visible {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.fadeInUp.js-visible::before {
  opacity: 1;
}

.fadeInLeft {
  opacity: 0;
  transform: translate3d(35px, 0, 0);
  transition: opacity 0.5s linear, transform 0.7s ease;
  transition-delay: 0.25s;
}
.fadeInLeft.js-visible {
  opacity: 1;
  transform: none;
}

.fadeInRight {
  opacity: 0;
  transform: translate3d(-35px, 0, 0);
  transition: opacity 1s linear, transform 1.1s ease;
  transition-delay: 0.25s;
}
.fadeInRight.js-visible {
  opacity: 1;
  transform: none;
}

.select-box {
  position: relative;
  height: 60px;
  width: 100%;
  max-width: 468px;
  border: 2px solid;
  font-size: 16px;
  background-color: #f2f2f2;
}
.select-box::before {
  z-index: 2;
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  border-bottom: 2px solid;
  border-left: 2px solid;
  top: 50%;
  transform: rotate(-45deg) translateY(-50%);
  right: 28px;
  flex-shrink: 0;
  pointer-events: none;
}
.select-box__current {
  position: relative;
  cursor: pointer;
  outline: none;
}
.select-box__current:focus + .select-box__list {
  opacity: 1;
  animation-name: none;
}
.select-box__current:focus + .select-box__list .select-box__option {
  cursor: pointer;
}
.select-box__current:focus .select-box__icon {
  transform: translateY(-50%) rotate(180deg);
}
.select-box__icon {
  position: absolute;
  top: 50%;
  right: 1px;
  transform: translateY(-50%);
  width: 14px;
  opacity: 1;
  transition: 0.2s ease;
}
.select-box__value {
  display: flex;
}
.select-box__input {
  display: none;
}
.select-box__input:checked + .select-box__input-text {
  display: block;
}
.select-box__input-text {
  display: none;
  width: 100%;
  margin: 0;
  padding: 15px 24px;
  background-color: #f2f2f2;
  font-weight: normal;
}
.select-box__list {
  border: 2px solid #373334;
  z-index: 5;
  position: absolute;
  width: calc(100% + 4px);
  left: -2px;
  padding: 0;
  height: auto;
  max-height: 250px;
  overflow-y: auto;
  list-style: none;
  opacity: 0;
  background-color: #f2f2f2;
  padding: 17px 24px 0;
  animation-name: HideList;
  animation-duration: 0.5s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: step-start;
}
.select-box__list::-webkit-scrollbar {
  width: 6px;
  appearance: none;
  -webkit-appearance: none;
}
.select-box__list::-webkit-scrollbar-track {
  border-radius: 2px;
  background: #959595;
}
.select-box__list::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #9F0023;
  border: 2px solid #9F0023;
}
.select-box__option {
  margin-bottom: 20px;
  display: block;
  font-weight: 500;
  transition: color 0.3s;
}
.select-box__option:hover, .select-box__option:focus {
  color: #9F0023;
}

@keyframes HideList {
  from {
    transform: scaleY(1);
  }
  to {
    transform: scaleY(0);
  }
}
.header__burger {
  width: 18px;
  height: 14px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  color: #373334;
}
.header__burger span {
  background-color: #373334;
  position: absolute;
  border-radius: 2px;
  transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  width: 100%;
  height: 2px;
  transition-duration: 500ms;
}
.header__burger span:nth-child(1) {
  top: 0px;
  left: 0px;
}
.header__burger span:nth-child(2) {
  top: 6px;
  left: 0px;
  opacity: 1;
}
.header__burger span:nth-child(3) {
  bottom: 0px;
  left: 0px;
}
.header__burger.opened span:nth-child(1) {
  transform: rotate(45deg);
  top: 7px;
}
.header__burger.opened span:nth-child(2) {
  opacity: 0;
}
.header__burger.opened span:nth-child(3) {
  transform: rotate(-45deg);
  top: 7px;
}

.mobilemenu-opened .mobilemenu {
  transform: translate(0);
}

.mobilemenu {
  z-index: 50;
  position: fixed;
  top: 0;
  left: 0;
  transition: transform 0.3s;
  transform: translate(-100%);
  width: 100%;
  max-width: 320px;
  height: 100vh;
  background-color: #373334;
  padding: 16px 22px 52px;
}
@media screen and (min-width: 880px) {
  .mobilemenu {
    display: none;
  }
}
.mobilemenu__burger {
  size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mobilemenu__burger::before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #fff;
  color: white;
}
