  filter: brightness(0) invert(1);
  size: 20px;
  background: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.25096 4.13824L2.99572 2.88305C2.76689 2.65422 2.76689 2.28325 2.99572 2.05442C3.22451 1.82559 3.59553 1.82563 3.82436 2.05442L5.0796 3.30961C5.30843 3.5384 5.30843 3.90942 5.0796 4.13824C4.85085 4.36703 4.47983 4.36711 4.25096 4.13824Z' fill='%239F0023' /%3E%3Cpath d='M1.52724 6.90356H2.78247C3.10606 6.90356 3.36841 7.16591 3.36841 7.4895C3.36841 7.8131 3.10606 8.07544 2.78247 8.07544H1.52724C1.20364 8.07544 0.941299 7.8131 0.941299 7.4895C0.941299 7.16591 1.20364 6.90356 1.52724 6.90356Z' fill='%239F0023' /%3E%3Cpath d='M8.43115 2.42707C8.10756 2.42707 7.84521 2.16473 7.84521 1.84113V0.585938C7.84521 0.262344 8.10756 0 8.43115 0C8.75475 0 9.01709 0.262344 9.01709 0.585938V1.84113C9.01709 2.16473 8.75475 2.42707 8.43115 2.42707Z' fill='%239F0023' /%3E%3Cpath d='M18.887 14.6066C19.1157 14.8351 19.1159 15.2064 18.887 15.4352L16.3766 17.9456C16.1479 18.1744 15.7769 18.1745 15.548 17.9456L14.5661 16.9638L17.9051 13.6248L18.887 14.6066Z' fill='%239F0023' /%3E%3Cpath d='M8.74235 15.0796L12.8567 17.0158L10.0441 19.8284C9.81532 20.0571 9.44427 20.0572 9.21544 19.8284L7.01884 17.6318C6.79001 17.4029 6.79001 17.032 7.01884 16.8031L8.74235 15.0796Z' fill='%239F0023' /%3E%3Cpath d='M11.1549 2.05455C11.2898 1.91962 11.482 1.85888 11.6699 1.89166C11.8579 1.92447 12.0181 2.04673 12.0994 2.21939L17.0767 12.7961L13.7376 16.1352L3.16088 11.1579C2.98822 11.0766 2.86596 10.9164 2.83315 10.7284C2.80033 10.5404 2.86112 10.3483 2.99604 10.2134L11.1549 2.05455Z' fill='%239F0023' /%3E%3C/svg%3E") no-repeat center;
  background-size: contain;
}
.mobilemenu * {
  color: white;
}
.mobilemenu__socials {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.mobilemenu__socials a {
  align-items: center;
  display: flex;
  size: 18px;
  flex-shrink: 0;
  flex-shrink: 0;
}
.mobilemenu__socials a svg {
  flex-shrink: 0;
  width: 100%;
  height: auto;
  transition: fill 0.3s;
  fill: #373334;
  filter: invert(1);
}
.mobilemenu__socials a:hover svg {
  fill: #9F0023;
}
.mobilemenu__socials a:not(:last-of-type) {
  margin-bottom: 17px;
}
.mobilemenu__content {
  align-items: start;
  height: 100%;
  overflow-y: scroll;
  display: grid;
  grid-template-columns: 1fr 30px;
  grid-template-rows: 45px 40px 1fr;
  grid-gap: 30px 15px;
}
.mobilemenu__content-left {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  min-height: 100%;
}
.mobilemenu__content-right {
  min-height: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.mobilemenu__header {
  width: 100%;
  grid-column: 1/-1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 15px;
  border-bottom: 2px solid;
}
.mobilemenu__logo {
  max-width: 104px;
  filter: brightness(0) invert(1);
}
.mobilemenu__logo svg {
  fill: white;
  width: 100%;
  height: auto;
}
.mobilemenu__search {
  grid-column: 1/-1;
  margin-bottom: 35px;
  display: flex;
  align-items: center;
  position: relative;
  height: 40px;
  border-bottom: 2px solid;
}
.mobilemenu__search svg {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 5px;
  pointer-events: none;
  fill: white;
}
.mobilemenu__search input {
  background-color: transparent;
  padding-left: 40px;
  height: 100%;
  width: 100%;
}
.mobilemenu__search input::-webkit-search-cancel-button {
  background-color: #fff;
  fill: white;
  color: white;
}
.mobilemenu__phone {
  margin-top: auto;
  font-weight: bold;
  display: flex;
  align-items: center;
}
.mobilemenu__phone::before {
  size: 12px;
  margin-right: 15px;
  content: "";
  background: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.220378 9.39992L2.9586 6.91031C3.22738 6.66579 3.64232 6.67859 3.8956 6.93927L5.50755 8.59699C5.89555 8.52289 6.67559 8.27973 7.47853 7.47882C8.28148 6.67522 8.52465 5.89317 8.59673 5.50788L6.94032 3.89529C6.67896 3.64201 6.66683 3.22775 6.91135 2.95831L9.40035 0.220817C9.64487 -0.0492949 10.0605 -0.0748915 10.3373 0.162214L11.7991 1.41578C11.9156 1.53298 11.985 1.68791 11.9944 1.85294C12.0045 2.02134 12.1972 6.01038 9.10396 9.10488C6.40547 11.8026 3.02529 12 2.09436 12C1.95829 12 1.87477 11.996 1.85254 11.9946C1.6875 11.9852 1.53257 11.9158 1.41604 11.7986L0.16312 10.3362C-0.0753374 10.0594 -0.0490665 9.64443 0.220378 9.39992Z' fill='white' /%3E%3C/svg%3E") no-repeat center;
  background-size: contain;
}
.mobilemenu__nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 40px;
}
.mobilemenu__nav > a {
  font-size: 16px;
  font-weight: bold;
}
.mobilemenu__nav > *:not(:last-child) {
  margin-bottom: 16px;
}
.mobilemenu__nav .openable {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-weight: bold;
}
.mobilemenu__nav .openable.opened ul {
  max-height: 200px;
  margin-top: 10px;
}
.mobilemenu__nav .openable.opened::after {
  top: 6px;
  transform: rotate(135deg);
}
.mobilemenu__nav .openable span {
  pointer-events: none;
  position: relative;
}
.mobilemenu__nav .openable span::after {
  transition: transform 0.3s, top 0.3s;
  top: 3px;
  right: -25px;
  position: absolute;
  content: "";
  width: 7px;
  height: 7px;
  margin-left: 20px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  transform: rotate(-45deg);
}
.mobilemenu__nav .openable ul {
  height: auto;
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.3s, margin-top 0.3s;
}
.mobilemenu__nav .openable ul li {
  margin-bottom: 10px;
}
.mobilemenu__nav .openable ul a {
  padding-left: 5px;
  font-weight: 300;
}

.breadcrumbs {
  display: flex;
  align-items: center;
  margin: 15px 0 35px;
  flex-wrap: wrap;
}
@media screen and (max-width: 767px) {
  .breadcrumbs {
    margin-top: 30px;
  }
}
.breadcrumbs-item {
  margin-bottom: 10px;
  color: #959595;
}
.breadcrumbs-item:not(:last-of-type)::after {
  content: "/";
  margin: 0 5px 10px;
}

.cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
  grid-gap: 80px 48px;
  grid-auto-flow: dense;
}
@media screen and (max-width: 768px) {
  .cards {
    grid-template-columns: repeat(auto-fill, minmax(275px, 1fr));
    grid-gap: 35px;
  }
}
.cards__item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.cards__item:hover .cards__item-text {
  color: #9F0023;
}
.cards__item:hover .cards__item-img img {
  transform: scale(1.03);
}
.cards__item.wide {
  background-color: #fff;
}
@media screen and (min-width: 1024px) {
  .cards__item.wide {
    padding: 45px;
    grid-column: 1/-1;
    flex-direction: row;
  }
}
@media screen and (max-width: 1023px) {
  .cards__item.wide {
    padding: 15px;
  }
}
@media screen and (min-width: 1024px) {
  .cards__item.wide .cards__item-img {
    margin-right: 50px;
    margin-bottom: 0;
    height: 264px;
    width: 100%;
    max-width: 468px;
    margin-bottom: 0;
    padding-top: 0;
  }
}
@media screen and (min-width: 1024px) {
  .cards__item.wide .cards__item-img img {
    position: static;
  }
}
@media screen and (min-width: 1024px) {
  .cards__item.wide .cards__item-text-additional {
    margin-top: auto;
    line-height: 1.6;
  }
}
@media screen and (max-width: 1023px) {
  .cards__item.wide .cards__item-content {
    margin-bottom: 20px;
  }
}
@media screen and (min-width: 1024px) {
  .cards__item.wide .cards__item-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    max-width: 600px;
  }
}
@media screen and (min-width: 1024px) {
  .cards__item.wide .cards__item-link {
    width: 22px;
    height: 22px;
    margin-left: auto;
  }
}
.cards__item-img {
  overflow: hidden;
  width: 100%;
  padding-top: 57%;
  position: relative;
  margin-bottom: 20px;
  background-color: #D2D2D2;
  background-image: url("../img/svg-sprite/plug.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.cards__item-img img {
